const Form = ({form, setForm, Project}) => {

  const options = [
    {value: 'Sim', caption: 'Sim'},
    {value: 'No', caption: 'No'}
  ]

  useEffect(() => {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/getData`, {step: Project.step, project_id: Project.project_id})
        .then(response => {
          setForm({...JSON.parse(response.data)});
        })
  }, [])

  return (
    <div className="form">
      <div className="left">
        <div className="formItem">
          <p>Atualmente, h um mercado definido, estabilizado e com grande volume de clientes e concorrentes para o seu produto?</p>
          <div className="input">
            <Select options={options} size={1.3} value={form.Q1} onChange={val => setForm({...form, Q1: val})} />
          </div>
        </div>
        <div className="formItem">  
          <p>Existem, nesse mercado, diversos grupos de clientes com prioridades diferentes em relao ao seu produto?</p>
          <div className="input">
          <Select options={options} size={1.3} value={form.Q2} onChange={val => setForm({...form, Q2: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>J existe, em algum outro lugar do mundo, algum pblico familiarizado com este produto?</p>
          <div className="input">
          <Select options={options} size={1.3} value={form.Q3} onChange={val => setForm({...form, Q3: val})} />
          </div>
        </div>
      </div>
    </div>
  )
}

const End = () => {
  return (
    <div className="end">
      <div className="left">
        <div className="diagnosys">
          <h1>Nosso Diagnstico</h1>
          <p><span>Ressignificao de Mercado</span><br /><br />A estratgia relacionada  ressignificao de mercado leva em conta a diversificao de clientes dentro do seu segmento. Para criar um produto de sucesso, foque inicialmente em um s nicho que tem a dor que voc resolve muito presente. Dessa forma, voc conseguir conquista-lo e ganhar autoridade para expandir para outros nichos.</p>
        </div>
        <div className="disclaimer">
          <h1>Exemplo Prtico</h1>
          <p>O Facebook escolheu uma faculdade para o lanamento da sua plataforma, quando o produto j era um sucesso, as pessoas comearam a pedir pela expanso. Dessa forma, o Facebook adicionou mais universidades ao portflio e, quando alcanou mais prestgio, abriu a rede social para todas as pessoas.</p>
        </div>
      </div>
      <div className="right">
        <div className="benchmark">
          <h1>Disclaimer</h1>
          <p>O objetivo desta ferramenta no  determinar se sua ideia  boa ou ruim, mas dar uma perspectiva, de acordo com um consenso do mercado, das suas possibilidades de sucesso. Por isso, os resultados no devem ser interpretados como uma resposta definitiva, mas como uma nova varivel que te ajudar a decidir se dever construir ou no uma empresa como esta.</p>
        </div>
      </div>
    </div>
  )
}

const Two = ({Project, showAlert, setProjectStep, setProjectCurrentStep}) => {

  const [page, setPage] = useState(1);
  const [form, setForm] = useState({
    Q1: '',
    Q2: '',
    Q3: ''
  })

  const handleNextButton = () => {
    if(Project.current_step !== 2) return setPage(page+1);
    let error = Object.values(form).some(item => item === '');
    if(error) {
      showAlert({
        title: 'Erro',
        message: 'Todos os campos so obrigatrios'
      })
    }
    else {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/save`, {step: Project.current_step, project_id: Project.project_id, data: JSON.stringify(form)})
        .then(response => {
          if(response.status === 200){
            setPage(page + 1);
          }
        })
    }
  }

  const handleFinishButton = () => {
    axios.post(`${process.env.REACT_APP_API_URL}api/projects/update/${Project.project_id}`, {data: {step: (Project.current_step + 1)}})
      .then(response => {
        if(response.status === 200) {
          setProjectCurrentStep(Project.current_step + 1);
          setProjectStep({step: (Project.step + 1)});
        }
      })
  }

  return (
    <Container colors={Colors}>
      {page === 1 && <Summary />}
      {page === 2 && <Form form={form} setForm={setForm} Project={Project}/>}
      {page === 3 && <End />}
      <Buttons>
        {page === 1 ?
          <div className="no-button"></div>  
        :
          <Button caption="Voltar" color={Colors.orange} onClick={() => setPage(page - 1)}/>  
        }
        <DotsPages page={page} pages={3} setPage={setPage}/>
        {page === 3 && Project.current_step === 2 && <Button caption="Concluir" color={Colors.green} onClick={handleFinishButton}/>}
        {page === 2 && <Button caption="Avanar" color={Colors.green} onClick={handleNextButton}/>}
        {page === 1 && <Button caption="Avanar" color={Colors.green} onClick={() => setPage(page + 1)}/>}
      </Buttons>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({
  showAlert: (props = {title: '', message: '', callback: undefined}) => dispatch({type: 'SHOW_ALERT', ...props}),
  setProjectStep: ({step = 0}) => dispatch({type: 'SET_PROJECT_STEP', step: step}),
  setProjectCurrentStep: (step = 0) => dispatch({type: 'SET_PROJECT_CURRENT_STEP', current_step: step})
});

export default connect(mapStateToProps, mapDispatchToProps)(Two);