import React from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';

import Colors from '../../../../Components/Colors';
import DotsPages from '../../../../Components/DotsPages';
import Button from '../../../../Components/Button';
import TextInput from '../../../../Components/TextInput';
import Modal from '../../../../Components/Modal'
import axios from 'axios';
import { useEffect } from 'react';

const Container = styled.div`
  animation: fadeIn 1s ease;
  .summary {
    display: flex;
    gap: 2rem;

    .left {
      background-color: #EEE;
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      div {
        display: flex;
        align-items: center;

        .caption {
          font-family: Montserrat-Bold;
          font-size: 7rem;
          min-width: 10rem;
        }
        .text {
          font-family: Montserrat-Medium;
          font-size: 1.8rem;
        }
      }
    }
    .right {
      background-color: #EEE;
      flex: 0.6;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      p {
        font-family: Montserrat-Medium;
        font-size: 1.8rem;
        line-height: 1.5;
      }
    }
  }
  .form {
    display: flex;
    gap: 2rem;
    .left {
      flex: 1;
      padding: 2rem;  
      background-color: #EEE;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .formItem {
        display: flex;
        align-items: center;
        justify-content: space-between;
        p {
          font-size: 1.6rem;
          font-family: Montserrat-Bold;
          min-width: 30rem;
        }
        .input {
          min-width: 40rem;
          margin-left: 3rem;
        }
        .help {
          border: 3px solid black;
          font-family: Montserrat-Bold;
          width: 3rem;
          height: 3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          font-size: 2rem;
          margin-left: 2rem;
          cursor: pointer;
        }
      }
    }
    .right {
      flex: 0.4;
      .materials {
        border: 2px solid #BBB;
        background-color: #EEE;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        padding: 2.5rem;
        h1 {
          font-size: 2.6rem;
          text-align: center;
        }
        div {
          display: flex;
          align-items: center;
          cursor: pointer;
          padding: 1rem;
          img {
            width: 4rem;
            height: 4rem;
          }
          p {
            font-size: 1.6rem;
            font-family: Montserrat-Medium;
            margin-left: 2rem;
          }
          &:hover {
            background-color: #BBB;
          }
        }
      }
      @media screen and (max-width: 1366px){
        flex: 0.5;
      }
    }
  }
  .end {
    display: flex;
    gap: 2rem;
    .left {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .diagnosys,
      .disclaimer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        h1 {
          margin: 0;
          font-size: 2.4rem;
          font-family: Montserrat-Bold;
        }
        p {
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
        }
      }
    }
    .right {
      flex: 0.3;
      display: flex;
      flex-direction: column;
      align-items: center;
      .grade,
      .benchmark {
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
          margin: 0;
          font-family: Montserrat-Bold;
          font-size: 2.4rem;
        }
        p {
          text-align: center;
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
        }
        h2 {
          text-align: center;
          font-size: 3rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Bold;
          color: #7ED957;
          min-width: 30rem;
        }
      }
    }
  }
`

const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  .no-button {
    min-width: 20rem;
  }
  .custom-button {
    max-width: 20rem;
  }
`

const FormModalContainer = styled.div`
  .top {
    display: flex;
    justify-content: space-between;
    h1 {
      font-size: 3rem;
      font-family: Montserrat-Bold;
    }
    p {
      color: rgba(0,0,0,0.5);
      font-size: 1.6rem;
      margin-right: 1rem;
      font-family: Montserrat-Bold;
      cursor: pointer;
    }
  }
  .content {
    p {
      font-family: Montserrat-Medium;
      font-size: 1.6rem;
    }
  }
`


const Summary = () => {
  return (
    <div className="summary">
      <div className="left">
        <h1>Como Usar?</h1>
        <div className="first">
          <div className="caption">1</div>
          <div className="text">Leia os relatrios da BusinessPal sobre o seu setor para conseguir uma estimativa do tamanho do MTD (Mercado Total Disponvel) e do MTP (Mercado Total Possvel).</div>
        </div>
        <div className="second">
          <div className="caption">2</div>
          <div className="text">Complemente sua pesquisa fazendo buscas na internet e coletando informaes sobre o setor.</div>
        </div>
        <div className="third">
          <div className="caption">3</div>
          <div className="text">Insira as informaes que encontrou nos campos da nossa ferramenta avaliadora de tamanho de mercado. Voc receber um diagnstico personalizado na mesma hora!</div>
        </div>
      </div>
      <div className="right">
        <h1>Por que  importante?</h1>
        <p>Avaliar o tamanho do mercado no qual voc pretende se inserir  essencial para metrificar as chances de sucesso de um novo empreendimento.</p>
        <p>Tanto a entrada em mercados muito pequenos como grandes demais podem trazer problemas de diferentes naturezas e diminuir as chances de sucesso de uma empresa que est comeando.</p>
      </div>

    </div>
  )
}

const Form = ({setFormModal, form, setForm, Project}) => {
  useEffect(() => {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/getData`, {step: Project.step, project_id: Project.project_id})
        .then(response => {
          setForm({...JSON.parse(response.data)});
        })
  }, [])

  return (
    <div className="form">
      <div className="left">
        <div className="formItem">
          <p>Valor Estimado para o MTD<br />(Mercado Total Disponvel)</p>
          <div className="input">
            <TextInput type="currency" placeholder="Insira valores em R$" size={1.3} value={form.MTD} onChange={val => setForm({...form, MTD: val})}/>
          </div>
          <div className="help">?</div>
        </div>
        <div className="formItem">  
          <p>Valor Estimado para o MTP<br />(Mercado Total Possvel)</p>
          <div className="input">
            <TextInput type="currency" placeholder="Insira valores em R$" size={1.3} value={form.MTP} onChange={val => setForm({...form, MTP: val})}/>
          </div>
          <div className="help">?</div>
        </div>
        <div className="formItem">
          <p>Valor Estimado para o MA<br />(MERCADO ALVO)</p>
          <div className="input">
            <TextInput type="currency" placeholder="Insira valores em R$" size={1.3} value={form.MA} onChange={val => setForm({...form, MA: val})}/>
          </div>
          <div className="help">?</div>
        </div>
      </div>
      <div className="right">
        <div className="materials">
          <h1>Materiais Complementares</h1>
          <div className="dados" onClick={() => setFormModal(true)}>
            <img src="images/icons/lupa.png" alt="" />
            <p>Onde posso achar esses dados?</p>
          </div>
          <div className="relatorios">
            <img src="images/icons/report.png" alt="" />
            <p>Relatrios BusinessPal</p>
          </div>
          <div className="blog">
            <img src="images/icons/text.png" alt="" />
            <p>Blog BusinessPal</p>
          </div>
          <div className="help">
            <img src="images/icons/telemarketing.png" alt="" />
            <p>Fale com um Consultor(a)</p>
          </div>
        </div>
      </div>
    </div>
  )
}
const FormModal = ({setFormModal}) => {
  return (
    <FormModalContainer>
      <div className="top">
        <h1>Onde consigo achar dados sobre meu mercado?</h1>
        <p onClick={() => setFormModal(false)}>X</p>
      </div>
      <div className="content">
        <p>Por mais que tentemos, ainda no conseguimos cobrir todos os setores da economia. Por isso, se voc ainda ficou com dvidas sobre o tamanho do seu mercado, vamos recomendar alguns sites confiveis que tambm fazem esse papel e, inclusive, podem te ajudar a obter estimativas mais precisas.</p>
      </div>
      <div className="icons"></div>
    </FormModalContainer>
  )
}

const End = ({Project}) => {
  return (
    <div className="end">
      <div className="left">
        <div className="diagnosys">
          <h1>Nosso Diagnstico</h1>
          <p>O objetivo desta ferramenta no  determinar se sua ideia  boa ou ruim, mas dar uma perspectiva, de acordo com um consenso do mercado, das suas possibilidades de sucesso. Por isso, os resultados no devem ser interpretados como uma resposta definitiva, mas como uma nova varivel que te ajudar a decidir se dever construir ou no uma empresa como esta.</p>
        </div>
        <div className="disclaimer">
          <h1>Disclaimer</h1>
          <p>O objetivo desta ferramenta no  determinar se sua ideia  boa ou ruim, mas dar uma perspectiva, de acordo com um consenso do mercado, das suas possibilidades de sucesso. Por isso, os resultados no devem ser interpretados como uma resposta definitiva, mas como uma nova varivel que te ajudar a decidir se dever construir ou no uma empresa como esta.</p>
        </div>
      </div>
      <div className="right">
        <div className="grade">
          <h1>Nota</h1>
          <h2>7,5</h2>
        </div>
        <div className="benchmark">
          <h1>Benchmark</h1>
          <p>Como parmetro, o MTD que buscamos deve ser maior do que R$ 10 bilhes, ao passo que o MTP eve ser maior do que R$ 1 bilho. Alm disso, o Mercado Alvo, de, pelo menos, R$ 500 milhes.</p>
        </div>
      </div>
    </div>
  )
}

const One = ({Project, showAlert, setProjectStep}) => {

  const [page, setPage] = useState(1);
  const [formModal, setFormModal] = useState(false)
  const [form, setForm] = useState({
    MTD: '',
    MTP: '',
    MA: ''
  })

  const handleNextButton = () => {
    if(Project.current_step !== 1) return setPage(page+1);
    let error = Object.values(form).some(item => item === '');
    if(error) {
      showAlert({
        title: 'Erro',
        message: 'Todos os campos so obrigatrios'
      })
    }
    else {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/save`, {step: Project.step, project_id: Project.project_id, data: JSON.stringify(form)})
        .then(response => {
          if(response.status === 200){
            setPage(page + 1);
          }
        })
    }
  }

  const handleFinishButton = () => {
    axios.post(`${process.env.REACT_APP_API_URL}api/projects/update/${Project.project_id}`, {data: {step: (Project.step + 1)}})
      .then(response => {
        if(response.status === 200) {
          setProjectStep({step: (Project.step + 1)})
        }
      })
  }

  return (
    <Container colors={Colors}>
      <Modal visible={formModal}>
        <FormModal setFormModal={setFormModal}/>
      </Modal>
      {page === 1 && <Summary />}
      {page === 2 && <Form setFormModal={setFormModal} form={form} setForm={setForm} Project={Project}/>}
      {page === 3 && <End Project={Project}/>}
      <Buttons>
        {page === 1 ?
          <div className="no-button"></div>  
        :
          <Button caption="Voltar" color={Colors.orange} onClick={() => setPage(page - 1)}/>  
        }
        <DotsPages page={page} pages={3} setPage={setPage}/>
        {page === 3 && Project.current_step === 1 && <Button caption="Concluir" color={Colors.green} onClick={handleFinishButton}/>}
        {page === 2 && <Button caption="Avanar" color={Colors.green} onClick={handleNextButton}/>}
        {page === 1 && <Button caption="Avanar" color={Colors.green} onClick={() => setPage(page + 1)}/>}
      </Buttons>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({
  showAlert: (props = {title: '', message: '', callback: undefined}) => dispatch({type: 'SHOW_ALERT', ...props}),
  setProjectStep: ({step = 0}) => dispatch({type: 'SET_PROJECT_STEP', step: step})
});

export default connect(mapStateToProps, mapDispatchToProps)(One);