import React, { useEffect } from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import axios from 'axios';

import Colors from '../../../../Components/Colors';
import DotsPages from '../../../../Components/DotsPages';
import Button from '../../../../Components/Button';
import TextInput from '../../../../Components/TextInput';
import Modal from '../../../../Components/Modal';
import Select from '../../../../Components/Select';


const Container = styled.div`
  animation: fadeIn 1s ease;
  .summary {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      background-color: #EEE;
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      div {
        display: flex;
        align-items: center;

        .caption {
          font-family: Montserrat-Bold;
          font-size: 7rem;
          min-width: 10rem;
        }
        .text {
          font-family: Montserrat-Medium;
          font-size: 1.8rem;
        }
      }
    }
    .right {
      background-color: #EEE;
      flex: 0.6;
      padding: 2rem;
      border-radius: 5px;
      h1 {
        font-family: Montserrat-Bold;
        font-size: 3rem;
      }
      p {
        font-family: Montserrat-Medium;
        font-size: 1.8rem;
        line-height: 1.5;
      }
    }
  }
  .form {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      flex: 1;
      padding: 2rem;  
      background-color: #EEE;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .formItem {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 1.6rem;
        p {
          font-size: 2rem;
          font-family: Montserrat-Bold;
          min-width: 30rem;
          .subtext {
            font-size: 1.4rem;
            color: rgba(0,0,0,0.7);
          }
        }
        .input {
          min-width: 50rem;
          margin-left: 3rem;
        }
        .help {
          border: 3px solid black;
          font-family: Montserrat-Bold;
          width: 3rem;
          height: 3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          font-size: 2rem;
          margin-left: 2rem;
          cursor: pointer;
        }
      }
    }
    .right {
      flex: 0.4;
      .materials {
        border: 2px solid #BBB;
        background-color: #EEE;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        padding: 2.5rem;
        h1 {
          font-size: 2.6rem;
          text-align: center;
        }
        div {
          display: flex;
          align-items: center;
          cursor: pointer;
          padding: 1rem;
          img {
            width: 4rem;
            height: 4rem;
          }
          p {
            font-size: 1.6rem;
            font-family: Montserrat-Medium;
            margin-left: 2rem;
          }
          &:hover {
            background-color: #BBB;
          }
        }
      }
      @media screen and (max-width: 1366px){
        flex: 0.5;
      }
    }
  }
  .end {
    display: flex;
    gap: 2rem;
    animation: fadeIn 0.3s ease;
    .left {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .diagnosys,
      .disclaimer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        h1 {
          margin: 0;
          font-size: 2.4rem;
          font-family: Montserrat-Bold;
        }
        p {
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
          span {
            color: #7ED957;
            font-size: 2.4rem;
            font-family: Montserrat-Bold;
          }
        }
      }
    }
    .right {
      flex: 0.25;
      display: flex;
      flex-direction: column;
      align-items: center;
      .grade,
      .benchmark {
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
          margin: 0;
          font-family: Montserrat-Bold;
          font-size: 2.4rem;
        }
        p {
          text-align: center;
          font-size: 1.6rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Medium;
          line-height: 1.3;
        }
        h2 {
          text-align: center;
          font-size: 3rem;
          background-color: #F8F8F8;
          padding: 2rem;
          font-family: Montserrat-Bold;
          color: #7ED957;
          min-width: 30rem;
        }
      }
    }
  }
`

const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  .no-button {
    min-width: 20rem;
  }
  .custom-button {
    max-width: 20rem;
  }
`

const Summary = () => {
  return (
    <div className="summary">
      <div className="left">
        <h1>Como Usar?</h1>
        <div className="first">
          <div className="caption">1</div>
          <div className="text">O ICP (Ideal Customer Profile)  a materializao do seu cliente ideal, aquela pessoa que voc tentar atingir para vender seu produto.</div>
        </div>
        <div className="second">
          <div className="caption">2</div>
          <div className="text">Adicione as variveis demogrficas correspondentes ao seu cliente-alvo.</div>
        </div>
        <div className="second">
          <div className="caption">3</div>
          <div className="text">Escolha uma foto que retrate bem qual  o seu pblico-alvo</div>
        </div>
        <div className="second">
          <div className="caption">4</div>
          <div className="text">Adicione atividades que seu pblico-alvo pratica com regularidade.</div>
        </div>
      </div>
      <div className="right">
        <h1>Por que  importante?</h1>
        <p>Nesse passo voc construir uma imagem do seu cliente ideal. Esse exerccio te ajudar a visualizar os melhores canais de aquisio para atingir esse pblico atravs da listagem de atividades frequentes que eles praticam.</p>
        <p>Alm disso, a construo do ICP  de suma importncia para balizar a comunicao entre a empresa e o cliente.</p>
      </div>

    </div>
  )
}

const Form = ({form, setForm, Project}) => {

  const options = [
    {value: 'Sim', caption: 'Sim'},
    {value: 'No', caption: 'No'}
  ]

  const mercado_options = [
    {value: 'Monoplio', caption: 'Monoplio'},
    {value: 'Duoplio', caption: 'Duoplio'},
    {value: 'Lder de Mercado', caption: 'Lder de Mercado'},
    {value: 'Mercado Voltil', caption: 'Mercado Voltil'},
    {value: 'Mercado Aberto', caption: 'Mercado Aberto'}
  ]

  const perspectiva_options = [
    {value: 'Muito Forte (+10% ao ano)', caption: 'Muito Forte (+10% ao ano)'},
    {value: 'Forte (Entre 5% e 10% ao ano)', caption: 'Forte (Entre 5% e 10% ao ano)'},
    {value: 'Estagnado (Entre 0% e 5% ao ano)', caption: 'Estagnado (Entre 0% e 5% ao ano)'},
    {value: 'Fraco (Entre -5% e 0% ao ano)', caption: 'Fraco (Entre -5% e 0% ao ano)'},
    {value: 'Muito Fraco (Entre -10% e -5% ao ano)', caption: 'Muito Fraco (Entre -10% e -5% ao ano)'}
  ]

  const indicios_options = [
    {value: 'Tenho fortes indcios', caption: 'Tenho fortes indcios'},
    {value: 'Tenho alguns indcios', caption: 'Tenho alguns indcios'},
    {value: 'Tenho poucos indcios', caption: 'Tenho poucos indcios'},
    {value: 'No tenho nenhum indcio', caption: 'No tenho nenhum indcio'}
  ]

  const certeza_options = [
    {value: 'Tenho 100% de certeza', caption: 'Tenho 100% de certeza'},
    {value: 'Tenho alguma certeza', caption: 'Tenho alguma certeza'},
    {value: 'Tenho pouca certeza', caption: 'Tenho pouca certeza'},
    {value: 'No tenho nenhuma certeza', caption: 'No tenho nenhuma certeza'}
  ]
  
  useEffect(() => {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/getData`, {step: Project.step, project_id: Project.project_id})
        .then(response => {
          setForm({...JSON.parse(response.data)});
        })
  }, [])

  return (
    <div className="form">
      <div className="left">
        <div className="formItem">
          <p>D uma nota de 0 a 10 para o tamanho do seu mercado<br /><span className="subtext">Recomendamos que d a nota que recebeu no Passo 1, quando avaliou o tamanho do seu mercado</span></p>
          <div className="input">
            <TextInput type="number" max="10" size={1.8} value={form.Q1} onChange={val => setForm({...form, Q1: val})} />
          </div>
        </div>
        <div className="formItem">  
          <p>Quo concentrado  o mercado em que voc pretende se inserir?<br /><span className="subtext">Recomendamos que siga o diagnstico que recebeu no Passo 3, quando avaliou a concentrao do seu mercado.</span></p>
          <div className="input">
            <Select options={mercado_options} size={1.3} value={form.Q2} onChange={val => setForm({...form, Q2: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Qual  a perspectiva de crescimento para os prximos anos neste mercado?<br /><span className="subtext">Recomendamos que leia os relatrios da BusinessPal para avaliar a perspectiva de crescimento futuro no seu segmento.</span></p>
          <div className="input">
            <Select options={perspectiva_options} size={1.3} value={form.Q3} onChange={val => setForm({...form, Q3: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Quanta certeza voc tem de que esse problema/desejo  real e latente?</p>
          <div className="input">
            <Select options={indicios_options} size={1.3} value={form.Q5} onChange={val => setForm({...form, Q5: val})} />
          </div>
        </div>
        <div className="formItem">
          <p>Quanta certeza voc tem de que consegue construir ou mobilizar um bom time para construir uma soluo para o problema/desejo que pretende resolver?</p>
          <div className="input">
            <Select options={certeza_options} size={1.3} value={form.Q4} onChange={val => setForm({...form, Q4: val})} />
          </div>
        </div>
      </div>
    </div>
  )
}

const End = () => {
  return (
    <div className="end">
      <div className="left">
        <div className="diagnosys">
          <h1>Nosso Diagnstico</h1>
          <p><span>Mercado Voltil</span><br /><br />Nessa circunstncia, a maior empresa do mercado ainda no  capaz de influenci-lo e, por isso, pode ser que existam tanto oportunidades em mercados existentes como em novos nichos. Dessa forma, voc deve analisar objetivamente a capacidade das empresas lderes do mercado de entregar o que  prometido. Caso elas faam um bom trabalho, isso  um indicativo de que voc deve buscar um nicho menor nesse mercado. Caso haja insatisfao de clientes,  um bom sinal de que aquele mercado existente necessita de novas empresas.</p>
        </div>
        <div className="disclaimer">
          <h1>Exemplo Prtico</h1>
          <p>A dr.Consulta, startup brasileira que oferece consultas, exames e procedimentos  baixo custo, percebeu que, no mercado de sade brasileiro, havia um espao muito grande entre as pessoas que podiam pagar por um plano de sade e as que tinham que utilizar o SUS. Dessa forma, a empresa criou um modelo que a permite cobrar um preo acessvel pelo atendimento mdico focado nas pessoas que estavam no meio das duas faixas de renda. Assim, apesar de concorrer com empresas grandes, a startup conseguiu prosperar diferenciando seu produto.</p>
        </div>
      </div>
      <div className="right">
        <div className="benchmark">
          <h1>Disclaimer</h1>
          <p>O objetivo desta ferramenta no  determinar se sua ideia  boa ou ruim, mas dar uma perspectiva, de acordo com um consenso do mercado, das suas possibilidades de sucesso. Por isso, os resultados no devem ser interpretados como uma resposta definitiva, mas como uma nova varivel que te ajudar a decidir se dever construir ou no uma empresa como esta.</p>
        </div>
      </div>
    </div>
  )
}

const Four = ({Project, showAlert, setProjectStep, setProjectCurrentStep}) => {

  const [page, setPage] = useState(1);
  const [form, setForm] = useState({
    Q1: '',
    Q2: '',
    Q3: '',
    Q4: '',
    Q5: ''
  })

  const handleNextButton = () => {
    if(Project.current_step !== 4) return setPage(page+1);
    let error = Object.values(form).some(item => item === '');
    if (form.Q1 > 10 || form.Q1 < 0) return showAlert({title: 'Erro', message: 'A nota deve ser entre 0 e 10'});
    if(error) {
      showAlert({
        title: 'Erro',
        message: 'Todos os campos so obrigatrios'
      })
    }
    else {
      axios.post(`${process.env.REACT_APP_API_URL}api/steps/save`, {step: Project.current_step, project_id: Project.project_id, data: JSON.stringify(form)})
        .then(response => {
          if(response.status === 200){
            setPage(page + 1);
          }
        })
    }
  }

  const handleFinishButton = () => {
    axios.post(`${process.env.REACT_APP_API_URL}api/projects/update/${Project.project_id}`, {data: {step: (Project.current_step + 1)}})
      .then(response => {
        if(response.status === 200) {
          setProjectCurrentStep(Project.current_step + 1);
          setProjectStep({step: (Project.step + 1)});
        }
      })
  }

  return (
    <Container colors={Colors}>
      {page === 1 && <Summary />}
      {page === 2 && <Form form={form} setForm={setForm} Project={Project}/>}
      {page === 3 && <End />}
      <Buttons>
        {page === 1 ?
          <div className="no-button"></div>  
        :
          <Button caption="Voltar" color={Colors.orange} onClick={() => setPage(page - 1)}/>  
        }
        <DotsPages page={page} pages={3} setPage={setPage}/>
        {page === 3 && Project.current_step === 4 && <Button caption="Concluir" color={Colors.green} onClick={handleFinishButton}/>}
        {page === 2 && <Button caption="Avanar" color={Colors.green} onClick={handleNextButton}/>}
        {page === 1 && <Button caption="Avanar" color={Colors.green} onClick={() => setPage(page + 1)}/>}
      </Buttons>
    </Container>
  )
}

const mapStateToProps = (state) => ({
  Project: state.ProjectReducer
});
const mapDispatchToProps = (dispatch) => ({
  showAlert: (props = {title: '', message: '', callback: undefined}) => dispatch({type: 'SHOW_ALERT', ...props}),
  setProjectStep: ({step = 0}) => dispatch({type: 'SET_PROJECT_STEP', step: step}),
  setProjectCurrentStep: (step = 0) => dispatch({type: 'SET_PROJECT_CURRENT_STEP', current_step: step})
});

export default connect(mapStateToProps, mapDispatchToProps)(Four);